.header {
}

.header__container {
    display: flex;
    justify-content: space-between;
}

.header__logo {
}

.header__nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.header__nav-list {
    display: flex;
    column-gap: 4.25em;
}

.header__list-item {
}

.header__item-link {
    @include font-patterns($black, "Mont", 1.37rem, normal, 600, 100%);
    margin-right: 4.25em;

    &:nth-last-child(1) {
        margin-right: 0;
    }
}

.header__phone-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header__phone-desc {
    @include font-patterns(rgba(30, 30, 30, 0.62), "Mont", 1rem, normal, 600, 100%);
    letter-spacing: 0.01em;
}

.header__phone-number {
    @include font-patterns(#1e1e1e, "Mont", 1.56rem, normal, 600, 100%);
}

.header__button {
    @include font-patterns(#fff, "Mont", 1.25rem, normal, 600, 100%);
    width: 289px;
    height: 77px;
    letter-spacing: 0.04em;
    background: #447c2a linear-gradient(102deg, rgba(61, 184, 70, 0) 0%, rgba(61, 184, 70, 0.8) 56.2%, rgba(61, 184, 70, 0) 100%);
    border-radius: 40px;
    box-shadow: 0 12px 12px 0 rgba(42, 124, 48, 0.34);
}