/* Down footer */
html,
body {
  height: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

/* Zeroing margins */
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

/*  MIXIN FO FONT ATTACHING  */
/*  MIXIN FOR FONT DECORATING  */
/*  MIXIN FOR GRADIENT  */
.header__container {
  display: flex;
  justify-content: space-between;
}

.header__nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.header__nav-list {
  display: flex;
  column-gap: 4.25em;
}

.header__item-link {
  color: #1e1e1e;
  font-weight: 600;
  font-size: 1.37rem;
  font-family: "Mont";
  font-style: normal;
  line-height: 100%;
  margin-right: 4.25em;
}
.header__item-link:nth-last-child(1) {
  margin-right: 0;
}

.header__phone-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header__phone-desc {
  color: rgba(30, 30, 30, 0.62);
  font-weight: 600;
  font-size: 1rem;
  font-family: "Mont";
  font-style: normal;
  line-height: 100%;
  letter-spacing: 0.01em;
}

.header__phone-number {
  color: #1e1e1e;
  font-weight: 600;
  font-size: 1.56rem;
  font-family: "Mont";
  font-style: normal;
  line-height: 100%;
}

.header__button {
  color: #fff;
  font-weight: 600;
  font-size: 1.25rem;
  font-family: "Mont";
  font-style: normal;
  line-height: 100%;
  width: 289px;
  height: 77px;
  letter-spacing: 0.04em;
  background: #447c2a linear-gradient(102deg, rgba(61, 184, 70, 0) 0%, rgba(61, 184, 70, 0.8) 56.2%, rgba(61, 184, 70, 0) 100%);
  border-radius: 40px;
  box-shadow: 0 12px 12px 0 rgba(42, 124, 48, 0.34);
}

